.Hours{
    position: relative;
}

.Hours__item{
    padding: 19px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}

.Hours__line{
    width: 100%;
    height: 1px;
    background-color: #9e9e9e91;
    margin-left: 15px
}

.Hours__text{
    width: 20px
}

.Hours__fill{
    /* position: absolute; */
    position: relative;
    width: 100%;
    height: 0;
    left: 34px;
    top: 20px;
    min-height: 30px;
    border-left: 2px solid #9c27b09e;
    color: white;
    font-weight: 600;
    font-size: 16px;
    padding: 4px 7px;
}

.Hours__wrapper{
    position: absolute;
    width: calc(100% - 34px);
    /* height: 100%; */
    display: -ms-flexbox;
    display: flex;
}

.Hours__fill.active{
    background: #9c27b061;
    z-index: 1;
}



